
.vc-toast {
  position: fixed;
  top: 90px;
  left: 50%;
  border-radius: 24px;
  height: 48px;
  z-index: 1100;
  background: #fdba78;
  color: #fff;
  line-height: 48px;
  box-shadow: 0 4px 9px 0 rgba(0,0,0,0.2);
}
.vc-toast .closebtn {
  color: #fff;
}
.vc-toast .type {
  font-size: 20px;
  margin-left: 20px;
  position: relative;
  top: 4px;
}
.vc-toast .close {
  font-size: 14px;
  margin-right: 20px;
  position: relative;
  top: 2px;
  cursor: pointer;
}
.vc-toast .ctn {
  display: inline-block;
  padding: 0 10px;
  height: 50px;
  font-size: 14px;
}
.vc-toast.danger {
  background: #f72b44;
}


