.vc-loader-wrapper {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.vc-loader-darkmask {
  background: rgba(0,0,0,0.1);
}
.vc-loader-directive {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.vc-loader {
  font-size: 40px;
  position: relative;
  width: 64px;
  height: 64px;
  overflow: hidden;
}
.vc-loader:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 2880px;
  height: 100%;
  background: url("./assets/loading.png") no-repeat 0 0;
  background-size: 2880px 128px;
  -webkit-animation: loadingAnimation 1200ms steps(44) infinite;
          animation: loadingAnimation 1200ms steps(44) infinite;
}
.vc-loader-small {
